<template>
        <header class="header">
                <div class="header-container">
                        <nuxt-link-locale :to="useRouteLink.INSIDE.HOME.path"><img src="@/assets/images/common/mft.png" class="logo"  alt=""/>
                        </nuxt-link-locale>
                        <div class="header-container__left">
                                <publicNavBar></publicNavBar>
                                <div class="btn-group" v-if="!isLogin">
                                        <nuxt-link-locale to="/login" class="login" plain rel="nofollow">
                                                {{ t('Login.title1') }}
                                        </nuxt-link-locale>
                                        <nuxt-link-locale to="/register" class="sign-up" rel="nofollow">
                                                {{ t('Login.title2') }}
                                        </nuxt-link-locale>
                                </div>
                                <template v-if="isLogin">
                                        <div class="btn-group" :lang="locale">
                                                <nuxt-link-locale to="/user" class="sign-up battle">{{ t('Login.btn1')
                                                        }}
                                                </nuxt-link-locale>
                                        </div>
                                        <div class="userWrap" @mouseover="bindUserHover" @mouseleave="bindUserLeave">
                                                <div class="head"><img alt="" src="@/assets/images/common/M.png" />
                                                </div>
                                                <div class="hide" :class="{ active: isUserSlide }">
                                                        <nuxt-link-locale class="item" to="/user">{{ t('Login.btn3')
                                                                }}
                                                        </nuxt-link-locale>
                                                        <p class="item" @click.stop="bindDoLoginOut">{{ t('Login.btn4')
                                                                }}</p>
                                                </div>
                                        </div>
                                </template>
                                <publicChangeLanguage></publicChangeLanguage>
                                <nuxt-link class="df discord" :to="discordUrl" target="_blank" data-platform="Discord"
                                        rel="nofollow">
                                        <img src="@/assets/images/common/icon.png" class="i-btn" alt="" />
                                        <img src="@/assets/images/common/icon_hover.png" alt="" class="icon-active" />
                                </nuxt-link>
                        </div>
                </div>
                <mobHeader></mobHeader>
                <el-backtop :bottom="100" />
        </header>
</template>
<script setup>
import { LANGUAGE_LIST } from '@/config/language.js'
import { FLAG_LIST } from "@/data/flag";
import mobHeader from './mobHead.vue'
import { ref } from 'vue'
import { useAccount } from '@/hooks/useAccount'
import { APP_DISCORD } from "@/config/app"
import { useRouteLink } from '@/composables/useRouteLink'
LANGUAGE_LIST.forEach((item) => {
        item.flagUrl = FLAG_LIST.find((item2) => item2.name === item.flag).flag
})

const { t, setLocale, locale } = useI18n()
const isUserSlide = ref(false)
//退出登录
const { isLogin, bindDoLoginOut } = useAccount()
//discord链接
const discordUrl = APP_DISCORD
const bindUserHover = _debounce(() => {
        isUserSlide.value = true
}, 500, { 'leading': false, 'trailing': true })
const bindUserLeave = () => {
        bindUserHover.cancel()
        isUserSlide.value = false
}
</script>
<style scoped lang="scss" src="./index.scss"></style>
