<template>
        <ul class="menu">
                <template v-for="(menu, index) in MENU_LIST" :key="index">
                        <li class="menu-item" v-if="!menu.children"
                            @click.stop="bindMenuTap(menu.path ? menu.path : '/', false, menu.active)"
                            :class="{ active: activeIdx === menu.active }">
                                {{ t(menu.label) }}
                        </li>
                        <li class="menu-item sub-menu" v-else :class="{ active: activeIdx === menu.active }">
                                <span class="menu-name"
                                      @click.stop="bindMenuTap(menu.path ? menu.path : '', menu.isBlank, menu.active)"
                                      v-if="menu.path">{{ t(menu.label) }}</span>
                                <span class="menu-name" v-else>{{ t(menu.label) }}</span>
                                <i class="iconfont icon-xiala arrow" v-if="menu.children"></i>
                                <ul class="sub-item">
                                        <template v-for="(menuItem, idx) in menu.children" :key="idx">
                                                <nuxt-link class="inner" v-if="menuItem.isBlank" :to="menuItem.path"
                                                           target="_blank" rel="nofollow">
                                                        {{ t(menuItem.label) }}
                                                </nuxt-link>
                                                <li class="inner"
                                                    @click.stop="bindMenuTap(menuItem.path ? menuItem.path : '/', menuItem.isBlank, menuItem.active)"
                                                    v-else>
                                                        {{ t(menuItem.label) }}
                                                </li>
                                        </template>
                                </ul>
                        </li>
                </template>
        </ul>
</template>
<script setup>
import { MENU_LIST } from '@/data/navbarMenus'
import { nuxtTo, getQuery } from '@/utils/tools'

const { t, locale } = useI18n()
const router = useRoute()
let activeIdx = ref(0)
const findCurrent = name => {
        let res = MENU_LIST.filter(item => (item.children ? item.children?.find(child => child.path.includes(name)) : item.path.includes(name)))
        return res[0]?.active
}
watch(
        () => router.fullPath,
        (newValue, oldValue) => {
                const pageName = newValue.split('/')
                let gotRouteName = locale.value !== 'en-us' ? pageName[2] : pageName[1]
                gotRouteName = gotRouteName?.includes("#") ? gotRouteName.split("#")[0] : gotRouteName
                activeIdx.value = gotRouteName ? findCurrent(gotRouteName) : 0
        },
        { immediate: true },
)
const bindMenuTap = (url, bol, idx) => {
        activeIdx.value = idx
        let query = url.includes('?') ? getQuery(url) : {}
        if (bol) {
                window.open(url, '_blank', 'rel=nofollow')
        } else {
                nuxtTo(url, query)
        }
}
</script>
<style scoped lang="scss" src="./index.scss"></style>
